.flex {
  display: flex;
}

.checkout-container {
  display: flex;
  align-items: center;
  justify-content: center;
  top: -1%;
  position: fixed;
  height: 101dvh;
  width: 100%;
  backdrop-filter: blur(10px);
}

.checkout-card {
  width: 75%;
  height: 80dvh;
}

.payment-methods {
  flex: 50%;
  flex-direction: column;
  align-items: center;
  background-color: var(--clr-accent-600);
  color: white;
  font-family: var(--ff-primary);
}

.succes-paymentContainer {
  flex-direction: column;
  align-items: center;
  margin-top: 10rem;
}

.succes-paymentContainer>h1 {
  font-size: 34px;
}

.succes-paymentContainer>p {
  opacity: 0.5;
}

.succes-paymentContainer>button {
  padding: 5px 50px;
  font-size: 18px;
  background-color: #58177c;
  color: white;
}

.succes-icon {
  height: 100px;
  width: 100px;
}

.order-details {
  position: relative;
  flex: 20%;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 10px 15px;
  background-color: var(--clr-accent-400);
  font-family: var(--ff-primary);
}

.order-details>h2,
h3 {
  color: white;
}

.close-btn {
  position: absolute;
  top: 2%;
  right: 0;
}

.title {
  text-align: center;
  position: relative;
}

.title::after,
.purchase-info::before {
  content: '';
  height: 2px;
  display: block;
  position: absolute;
  width: 100%;
  background-color: black;
}

.products-container {
  gap: 10px;
  height: fit-content;
  flex-wrap: wrap;
  flex-direction: column;
}

.purchase-info {
  padding: 20px;
  flex-direction: column;
  position: relative;
  gap: 5px;
  color: antiquewhite;
  width: 100%;
}

.purchase-info>* {
  margin: 0;
}

.faded {
  color: rgba(245, 245, 245, 0.568);
}